body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: -webkit-linear-gradient(right,rgba(7,27,82,1) 0%,rgba(0,128,128,1) 100%);
  background: linear-gradient(to left,rgba(7,27,82,1) 0%,rgba(0,128,128,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'SEGA-LOGO-FONT';
    src:url(/static/media/SEGA-LOGO-FONT.ttf.3f5c9111.woff) format('woff'),        
        url(/static/media/SEGA-LOGO-FONT.ttf.57ad6cb1.eot),
        url(/static/media/SEGA-LOGO-FONT.ttf.57ad6cb1.eot?#iefix) format('embedded-opentype'); 
        font-weight: normal;
        font-style: normal;
}


h1{
    font-family: 'SEGA-LOGO-FONT';

    font-weight: 200;
    color: #0ccac4;
}
